<template>
  <div>
    <div class="add-form-wraper">
      <el-form ref="form" :model="addParamMesg" label-width="140px">
        <el-form-item label="配送公司">
          <el-input v-model="addParamMesg.logisticsCompanyName" size="small" class="add-input" disabled></el-input>
          <span>编辑时配送公司不可更换</span>
        </el-form-item>
        <el-form-item label="选择门店" required>
          <el-input v-model="addParamMesg.storeName" size="small" class="add-input" disabled></el-input>
          <span>编辑时门店不可更换</span>
        </el-form-item>
        <el-form-item label="门店区域" required v-if="addParamMesg.storeNo && addParamMesg.district">
          <el-input v-model="cityStr" size="small" class="add-input-inline" disabled></el-input>
          <el-input v-model="addParamMesg.district" size="small" class="add-input-inline"></el-input>
          <span>地图中门店地址的区域和第三方平台区域不一致时可修改区域</span>
        </el-form-item>
        <el-form-item label="派单优先级" required>
          <el-select class="add-input" v-model="addParamMesg.sortNum" placeholder="请选择派单优先级" size="small" clearable>
            <el-option :label="item.label" :value="item.value" :key="item.value" v-for="item in sortNumOptList"></el-option>
          </el-select>
          <span>请选择派单优先级，越小越优先，如果选择的优先级已有其他的配送公司，保存后则自动将其他的配送公司降级</span>
        </el-form-item>
        <el-form-item label="立即送时间" required>
          <el-time-picker
            v-model="addParamMesg.dcOpenTime"
            format="HH:mm:ss"
            value-format="HH:mm:ss"
            class="add-input-inline"
            size="small" 
            clearable
            placeholder="最早可接单时间">
          </el-time-picker>
          -
          <el-time-picker
            v-model="addParamMesg.dcCloseTime"
            format="HH:mm:ss"
            class="add-input-inline"
            value-format="HH:mm:ss"
            size="small" 
            clearable
            placeholder="最晚可接单时间">
          </el-time-picker>
          <span>每天骑手最早的接单时间和最晚的可接单时间</span>
        </el-form-item> 
        <el-form-item label="配送范围" required>
          <!-- 门店自配送 -->
          <div class="h-btn-w" v-if="addParamMesg.logisticsCompanyId == 1001 && addParamMesg.storeNo">
            <div>   
              <el-button size="small" type="text" @click="polyEditorOpen" icon="el-icon-edit" style="margin-right:20px;">编辑配送范围</el-button>
            </div>
            <div style="margin-right: 10px;">
              查看配送范围:
            </div>
            <div>
              <el-checkbox v-model="mtChecked" @change = "mtCheckedChange">美团配送(<span style="color:#FF0000;">—</span>)</el-checkbox>
              <el-checkbox v-model="ddChecked" @change = "ddCheckedChange">达达配送(<span style="color:#67C23A;">—</span>)</el-checkbox>
              <el-checkbox v-model="sfChecked" @change = "sfCheckedChange">顺丰同城配送(<span style="color:#F39800;">—</span>)</el-checkbox>
            </div>
          </div>
          <!-- 美团配送 -->
          <div class="h-btn-w" v-if="addParamMesg.logisticsCompanyId == 1002 && addParamMesg.storeNo">
            <div style="margin-right: 10px;">
              查看配送范围:
            </div>
            <div>
              <el-checkbox v-model="blxChecked" @change = "blxCheckedChange">门店自配送(<span style="color:#3080fe;">—</span>)</el-checkbox>
              <el-checkbox v-model="mtChecked" @change = "mtCheckedChange">美团配送(<span style="color:#FF0000;">—</span>)</el-checkbox>
              <el-checkbox v-model="ddChecked" @change = "ddCheckedChange">达达配送(<span style="color:#67C23A;">—</span>)</el-checkbox>
              <el-checkbox v-model="sfChecked" @change = "sfCheckedChange">顺丰同城配送(<span style="color:#F39800;">—</span>)</el-checkbox>
            </div>
          </div>
          <!-- 达达配送 -->
          <div class="h-btn-w" v-if="addParamMesg.logisticsCompanyId == 1003 && addParamMesg.storeNo">
            <div>   
              <el-button size="small" type="text" @click="polyEditorOpen" icon="el-icon-edit" style="margin-right:20px;">编辑配送范围</el-button>
            </div>
            <div style="margin-right: 10px;">
              查看配送范围:
            </div>
            <div>
              <el-checkbox v-model="blxChecked" @change = "blxCheckedChange">门店自配送(<span style="color:#3080fe;">—</span>)</el-checkbox>
              <el-checkbox v-model="mtChecked" @change = "mtCheckedChange">美团配送(<span style="color:#FF0000;">—</span>)</el-checkbox>
              <el-checkbox v-model="sfChecked" @change = "sfCheckedChange">顺丰同城配送(<span style="color:#F39800;">—</span>)</el-checkbox>
            </div>
          </div>
          <!-- 顺丰同城配送 -->
          <div class="h-btn-w" v-if="addParamMesg.logisticsCompanyId == 1004 && addParamMesg.storeNo">
            <div>  
              <el-button size="small" type="text" @click="polyEditorOpen" icon="el-icon-edit" :disabled="!addParamMesg.storeNo" style="margin-right:20px;">编辑配送范围</el-button>
            </div>
            <div style="margin-right: 10px;">
              查看配送范围:
            </div>
            <div>
              <el-checkbox v-model="mtChecked" @change = "mtCheckedChange">美团配送(<span style="color:#FF0000;">—</span>)</el-checkbox>
              <el-checkbox v-model="blxChecked" @change = "blxCheckedChange">门店自配送(<span style="color:#409EFF;">—</span>)</el-checkbox>
              <el-checkbox v-model="ddChecked" @change = "ddCheckedChange">达达配送(<span style="color:#67C23A;">—</span>)</el-checkbox>
            </div>
          </div>

          <div class="blx-map-contianer">
            <div class="reference-range-wrap" v-if="addParamMesg.storeNo">
              参考范围：以门店为中心半径
              <el-input size="mini" v-model="radiusNum" class="add-input-inline-left"></el-input>
              公里
              <el-button size="mini" type="primary" @click="createCircleMarkGroup" :disabled="!radiusNum">显示</el-button>
            </div>
            <div id="mapContainer" tabindex="0"></div>
          </div>
        </el-form-item>
        <hr class="s-slip-tb">
        <el-form-item>
          <el-button type="primary" size="small" @click="doSaveHandel" :disabled="isDisabled">保存</el-button>
          <el-button size="small" @click="doCancelHandel">取消</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>
  import Storage from '@/utils/localStorage'
  import * as utils from '@/utils/utils'
  const reqUrl = {
    // 获取城市下门店过滤配送公司
    selectCityStore: '/plateform/a/mall/storeLogistics/selectCityStore',
    // 保存配置信息
    saveLogistics: '/plateform/a/mall/storeLogistics/saveLogistics',
    // 获取门店三方配置信息
    getStoreLogisticsScope: '/plateform/a/mall/storeLogistics/getStoreLogisticsScope',
    // 获取详情
    detail: '/plateform/a/mall/storeLogistics/detail'
  }

  let map;
  let polyEditor;
  // 编辑门店自配送、达达配送覆盖物
  let polygon;
  let marker;
  let infoWindow;
  // 门店自配送覆盖物
  let blxPolygon;
  // 美团覆盖物
  let mtPolygon;
  // 达达覆盖物
  let ddPolygon;
  // 顺丰覆盖物
  let sfPolygon;
  // 根据经纬度获取详细地址
  let geocoder;

  // 圆形
  let circle;
  export default {
    data() {
      return {
        cityStr: Storage.get('cityName'),
        isDisabled: false,
        addParamMesg: {
          id: '', // 当前配送信息的id
          dcCloseTime: '', // 配送结束时间 格式化 HH:mm:ss
          dcOpenTime: '', // 配送开始时间 格式化 HH:mm:ss
          dcScope: [], // 绘制的范围的点
          logisticsCompanyId: '', // 配送公司ID 1001-门店自配送 1002-美团
          logisticsCompanyName: '', // 配送公司名称
          sortNum: '', // 优先级
          storeNo: '', // ERP门店编码
          storeName: '', // 门店名称
          district: '' // 门店区域
        },
        storeOptions: [], // 门店信息
        logisticsCompanyOptList: [
          {
            label: '门店自配送',
            value: 1001
          },
          {
            label: '美团配送',
            value: 1002
          },
          {
            label: '达达配送',
            value: 1003
          },
          {
            label: '顺丰同城配送',
            value: 1004
          }
        ],
        sortNumOptList: [
          {
            label: '1',
            value: 1
          },
          {
            label: '2',
            value: 2
          },
          {
            label: '3',
            value: 3
          }
        ],
        // 门店位置坐标 默认北京市天安门
        storeLocation: {
          longitude: 116.403322,
          latitude: 39.920255
        },
        // 第三方的配送范围信息
        thirdDcScopeList: [],
        // 美团配送勾选没勾选
        mtChecked: false,
        // 本来鲜配送勾选没勾选
        blxChecked: false,
        // 达达配送勾选没勾选
        ddChecked: false,
        // 顺丰同城配送勾选没勾选
        sfChecked: false,

        // 半径范围
        radiusNum: ''
      }
    },
    created() {
      this.addParamMesg.id = this.$route.params.id || ''
      this.addParamMesg.storeNo = this.$route.params.storeNo || ''
    },
    mounted() {
      // 获取三方配送范围配置信息
      this.getStoreLogisticsScope()
      this.getDetailMsg()
    },
    methods: {
      /**
       * [getDetailMsg 获取详情]
       * @return {[type]} [description]
       */
      getDetailMsg() {
        utils.showLoading()
        this.$http.post(reqUrl.detail, {id: this.addParamMesg.id, storeNo: this.addParamMesg.storeNo}).then(res => {
          utils.clearLoading()
          if(res.code === 0) {
            if(res.data) {
              let createMesg = res.data
              this.addParamMesg.logisticsCompanyId = createMesg.logisticsCompanyId
              this.addParamMesg.logisticsCompanyName = createMesg.logisticsCompanyName
              this.addParamMesg.storeNo = createMesg.storeNo
              this.addParamMesg.storeName = createMesg.storeName
              this.addParamMesg.sortNum = createMesg.sortNum
              this.addParamMesg.dcOpenTime = createMesg.dcOpenTime
              this.addParamMesg.dcCloseTime = createMesg.dcCloseTime
              if(createMesg.dcScope) {
                this.addParamMesg.dcScope = JSON.parse(createMesg.dcScope) || []
              }

              if(createMesg.latitude && createMesg.longitude) {
                this.storeLocation.longitude = createMesg.longitude
                this.storeLocation.latitude = createMesg.latitude
              }else {
                if(createMesg.latitude == 0 || createMesg.longitude == 0) {
                  this.storeLocation.longitude = createMesg.longitude
                  this.storeLocation.latitude = createMesg.latitude
                }
              }

              // 重置地图
              this.initMap()
              // 当配送公司已选为自配送并且门店切换的时候
              // 1、创建marker
              // 2、创建多边形编辑器
              if(this.addParamMesg.storeNo) {
                // 1、创建marker
                this.createMarker()
              }
              let logisticsCompanyId = this.addParamMesg.logisticsCompanyId
              let storeNo = this.addParamMesg.storeNo
              if((storeNo && logisticsCompanyId === 1001) || (storeNo && logisticsCompanyId === 1003) || (storeNo && logisticsCompanyId === 1004)) {
                let strokeColor = ''
                let fillColor = ''
                if(logisticsCompanyId === 1001) {
                  strokeColor = '#3080fe'
                  fillColor = '#3080fe'
                }else if(logisticsCompanyId === 1003){
                  strokeColor = '#67C23A'
                  fillColor = '#67C23A'
                }else if(logisticsCompanyId === 1004){
                  strokeColor = '#F39800'
                  fillColor = '#F39800'
                }
                // 2、创建多边形编辑器
                this.createPolyEditor(strokeColor, fillColor)
              }else if(storeNo && logisticsCompanyId === 1002) {
                // 3、显示美团区域面积
                setTimeout(() => {
                  this.mtChecked = true
                  this.mtCheckedChange(true)
                }, 500)
              }
            }
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          utils.clearLoading()
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * [getStoreLogisticsScope 获取门店三方配置信息]
       * @return {[type]} [description]
       */
      getStoreLogisticsScope() {
        // 将查看按钮置空
        this.platIdList = []
        this.$http.post(reqUrl.getStoreLogisticsScope, {storeNo: this.addParamMesg.storeNo}).then(res => {
          if(res.code === 0) {
            if(res.data && res.data.length) {
              this.thirdDcScopeList = res.data
            }else {
              this.thirdDcScopeList = []
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("提示(获取门店三方配置信息):", e.message)
        })
      },
      /**
       * [init 地图初始化]
       * @return {[type]} [description]
       */
      initMap() {
        let lnglat = new AMap.LngLat(this.storeLocation.longitude, this.storeLocation.latitude)
        map = new AMap.Map('mapContainer', {
          center: [lnglat.lng, lnglat.lat],
          resizeEnable: true,
          zoom: 12
        })
        AMap.plugin(['AMap.ToolBar', 'AMap.Scale', 'AMap.PolyEditor', 'AMap.LngLat', 'AMap.Geocoder'], function() {
          map.addControl(new AMap.ToolBar())
          map.addControl(new AMap.Scale())
          map.addControl(new AMap.Geocoder())
        })  
      },
      /**
       * [createMarker 生成页面数据]
       * @return {[type]} [description]
       */
      createMarker() {
        let _this = this
        let lnglat = new AMap.LngLat(this.storeLocation.longitude, this.storeLocation.latitude)
        // 添加Marker
        marker = new AMap.Marker({
          position: [lnglat.lng, lnglat.lat]
        });
        map.add(marker);
        // 信息提示窗口
        infoWindow = new AMap.InfoWindow({
          autoMove: true,
          offset: {x: 0, y: -30}
        })
        // 创建infoWindow窗体
        infoWindow.setContent(`${this.addParamMesg.storeNo}-${this.addParamMesg.storeName}`);
        // 展开窗体
        infoWindow.open(map, marker.getPosition());

        let lnglatXY = [lnglat.lng, lnglat.lat];//地图上所标点的坐标
        geocoder = new AMap.Geocoder({
        });
        geocoder.getAddress(lnglatXY, function (status, result) {
          if (status === 'complete' && result.info === 'OK') {
            console.log("result.regeocode:", result.regeocode)
            let district = result.regeocode.addressComponent.district
            _this.addParamMesg.district = district
          } else {
            //获取地址失败
            _this.addParamMesg.district = ''
          }
        });
      },
      /**
       * [createPolyEditor 创建多边形编辑器]
       * @return {[type]} [description]
       */
      createPolyEditor(_strokeColor, _fillColor) {
        let strokeColor = _strokeColor
        let fillColor = _fillColor
        let path = []
        let lnglat = new AMap.LngLat(this.storeLocation.longitude, this.storeLocation.latitude)
        let pathArrTemp = this.addParamMesg.dcScope
        for(let item of pathArrTemp) {
          path.push(new AMap.LngLat(item.y, item.x))
        }
        polygon = new AMap.Polygon({
          path: path,
          strokeColor: strokeColor, 
          strokeWeight: 6,
          strokeOpacity: 1,
          fillOpacity: 0.4,
          fillColor: fillColor,
          zIndex: 50,
        })
        map.add(polygon)
        // 缩放地图到合适的视野级别
        map.setFitView([ polygon ])
        // 实例化编辑工具
        // polyEditor = new AMap.PolyEditor(map, polygon)
        AMap.plugin(['AMap.PolyEditor'], function() {
          polyEditor = new AMap.PolyEditor(map, polygon)
        })  
      },
      /**
       * [createCircleMarkGroup 创建圆形覆盖物共参考用]
       * @return {[type]} [description]
       */
      createCircleMarkGroup() {
        if(circle) {
          map.remove([ circle ])
        }

        let salePriceReg = /((^[1-9]\d*)|^0)(\.\d{0,2}){0,1}$/
        if(!this.addParamMesg.storeNo) {
          this.$message("请先选择门店")
          return
        }

        if(!this.radiusNum) {
          this.$message("请输入半径值")
          return 
        }else {
          if(this.radiusNum === '0') {
            this.$message("半径值为为大于0，小数保留小数点后2位")
            return
          }else if(!salePriceReg.test(this.radiusNum)) {
            this.$message("半径值为为大于0，小数保留小数点后2位")
            return
          }
        } 

        let radiusVal = parseInt(this.radiusNum) * 1000
        let lnglat = new AMap.LngLat(this.storeLocation.longitude, this.storeLocation.latitude)
        circle = new AMap.Circle({
            center: [lnglat.lng, lnglat.lat],// 圆心位置
            radius: radiusVal, //半径
            strokeColor: "#a0a0a0", //线颜色
            strokeOpacity: 1, //线透明度
            strokeWeight: 2, //线粗细度
            fillColor: "#FFFFFF", //填充颜色
            fillOpacity: 0, //填充透明度
            strokeStyle: 'dashed',
            zIndex: 60
        });
        map.add(circle)
        // 缩放地图到合适的视野级别
        map.setFitView([ circle ])
      },
      /**
       * [polyEditorOpen 开始编辑地图]
       * @return {[type]} [description]
       */
      polyEditorOpen() {
        polyEditor.open()
      },
      /**
       * [mtCheckedChange 美团配送选没选中]
       * @param  {[type]} _val [description]
       * @return {[type]}      [description]
       */
      mtCheckedChange(_val) {
        if(mtPolygon) {
          map.remove([ mtPolygon ])
        }
        let val = _val
        let logisticsCompanyId = 1002
        let strokeColor = "#FF0000"
        let fillColor = "#FF0000"
        this.createPolygon(val, logisticsCompanyId, strokeColor, fillColor)
      },
      /**
       * [blxCheckedChange 本来鲜自配送配送选没选中]
       * @return {[type]} [description]
       */
      blxCheckedChange(_val) {
        if(blxPolygon) {
          map.remove([ blxPolygon ])
        }
        let val = _val
        let logisticsCompanyId = 1001
        let strokeColor = "#3080fe"
        let fillColor = "#3080fe"
        this.createPolygon(val, logisticsCompanyId, strokeColor, fillColor)
      },
      /**
       * [ddCheckedChange 达达配送选没选中]
       * @return {[type]} [description]
       */
      ddCheckedChange(_val) {
        if(ddPolygon) {
          map.remove([ ddPolygon ])
        }
        let val = _val
        let logisticsCompanyId = 1003
        let strokeColor = "#67C23A"
        let fillColor = "#67C23A"
        this.createPolygon(val, logisticsCompanyId, strokeColor, fillColor)
      },
      /**
       * @description: sfCheckedChange 顺丰同城配送选没选中
       * @param {type} 
       * @return: 
       */
       sfCheckedChange(_val) {
        if(sfPolygon) {
          map.remove([ sfPolygon ])
        }
        let val = _val
        let logisticsCompanyId = 1004
        let strokeColor = "#F39800"
        let fillColor = "#F39800"
        this.createPolygon(val, logisticsCompanyId, strokeColor, fillColor)
      },
      /**
       * [createPolygon 创建覆盖物]
       * @param  {[type]} _val                [按钮是选中还是取消]
       * @param  {[type]} _logisticsCompanyId [配送公司ID 1001-门店自配送 1002-美团 1003-达达]
       * @param  {[type]} _strokeColor        [边框颜色]
       * @param  {[type]} _fillColor          [填充物颜色]
       * @return {[type]}                     [description]
       */
      createPolygon(_val, _logisticsCompanyId, _strokeColor, _fillColor) {
        let val = _val
        let logisticsCompanyId = _logisticsCompanyId
        let strokeColor = _strokeColor
        let fillColor = _fillColor

        let scopeMsg = this.thirdDcScopeList.filter(item => {
          return item.logisticsCompanyId === logisticsCompanyId
        })[0];
        if(scopeMsg) {
          let pathArr = []
          let pathArrTemp = JSON.parse(scopeMsg.dcScope)
          for(let item of pathArrTemp) {
            pathArr.push(new AMap.LngLat(item.y, item.x))
          }
          if(_val === true) {
            let platPolygon = new AMap.Polygon({
              path: pathArr,
              strokeColor: strokeColor, 
              strokeWeight: 6,
              strokeOpacity: 1,
              fillOpacity: 0.4,
              fillColor: fillColor,
              zIndex: 50,
            })
            if(logisticsCompanyId === 1001) {
              blxPolygon = platPolygon
              map.add([ blxPolygon ])
              // 缩放地图到合适的视野级别
              map.setFitView([ blxPolygon ])
            }else if(logisticsCompanyId === 1002) {
              mtPolygon = platPolygon
              map.add([ mtPolygon ])
              // 缩放地图到合适的视野级别
              map.setFitView([ mtPolygon ])
            }else if(logisticsCompanyId === 1003) {
              ddPolygon = platPolygon
              map.add([ ddPolygon ])
              // 缩放地图到合适的视野级别
              map.setFitView([ ddPolygon ])
            }
          }else {
            if(logisticsCompanyId === 1001) {
              map.remove([ blxPolygon ])
            }else if(logisticsCompanyId === 1002) {
              map.remove([ mtPolygon ])
            }else if(logisticsCompanyId === 1003) {
              map.remove([ ddPolygon ])
            }
          }
        }else {
          let devliveryStr = ''
          if(logisticsCompanyId === 1001) {
            devliveryStr = '门店自配送'
          }else if(logisticsCompanyId === 1002) {
            devliveryStr = '美团配送'
          }else if(logisticsCompanyId === 1003) {
            devliveryStr = '达达配送'
          }else if(logisticsCompanyId === 1004) {
            devliveryStr = '顺丰同城配送'
          }
          if(_val === true) {
            this.$notify.warning({
              title: '温馨提示',
              message: `该门店暂未设置${devliveryStr}范围`
            })
          }
          return
        }
      },
      /**
       * [doSaveHandel 保存操作]
       * @return {[type]} [description]
       */
      doSaveHandel() {
        if(polyEditor && polygon) {
          // 关闭绘图
          polyEditor.close()
          // 获取绘制的点
          let dcScopeMsg = polygon.getPath()

          let dcScopeList = []
          for(let item of dcScopeMsg) {
            let pointItemMsg = {
              x: item.lat,
              y: item.lng
            }
            dcScopeList.push(pointItemMsg)
          }
          this.addParamMesg.dcScope = dcScopeList
        }

        // 所有添加参数处理完毕，开始保存数据
        if(!this.addParamMesg.logisticsCompanyId) {
          this.$message("请选择配送公司")
          return
        }
        if(!this.addParamMesg.storeNo) {
          this.$message("请选择门店")
          return
        }
        if(!this.addParamMesg.sortNum) {
          this.$message("请选择派单优先级")
          return
        }
        if(!this.addParamMesg.dcOpenTime || !this.addParamMesg.dcCloseTime) {
          this.$message("请选择立即送时间")
          return
        }
        if(utils.changeStrToMinutes(this.addParamMesg.dcCloseTime) <= utils.changeStrToMinutes(this.addParamMesg.dcOpenTime)) {
          this.$message("最晚可接单时间不能早于最早可接单时间")
          return
        }
        if(this.addParamMesg.logisticsCompanyId == 1001) {
          // 门店自配送 要校验是否画配送范围
          if(this.addParamMesg.dcScope.length <= 0) {
            this.$message("请在地图上编辑门店自配送范围")
            return
          }
        }
        utils.showLoading()
        this.isDisabled = true
        this.$http.post(reqUrl.saveLogistics, {...this.addParamMesg}).then(res => {
          utils.clearLoading()
          // 保存按钮可再次点击
          setTimeout(() => {
            this.isDisabled = false
          }, 800)
          if(res.code === 0) {
            this.$message.success("保存成功")
            this.$router.push('/blx_delivery')
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          utils.clearLoading()
          // 保存按钮可再次点击
          this.isDisabled = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })

      },
      doCancelHandel() {
        this.$confirm('确定取消吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$router.push('/blx_delivery')
        }).catch(() => {})
      }
    },
    /**
     * [beforeRouteLeave 页面离开]
     * @param  {[type]}   to   [description]
     * @param  {[type]}   from [description]
     * @param  {Function} next [description]
     * @return {[type]}        [description]
     */
    beforeRouteLeave (to, from, next){
      to.meta.keepAlive = true;
      next();
    }
  }

</script>


